	module.exports = {"data":{"site":{"siteMetadata":{"title":"MyBlog"}},"markdownRemark":{"html":"<h1>IEC61850 server with a Beaglebone Black</h1>\n<h2>IEC61850 Server</h2>\n<p>We will cross-compile the server file using a Ubuntu 17.10 machine:</p>\n<pre><code class=\"language-bash\">    sudo apt-get install gcc-arm-linux-gnueabi gcc-arm-linux-gnueabihf\n    cd demos/beaglebone\n    make TARGET=LINUX-ARM\n    scp beagle_demo debian@192.168.7.2:/var/lib/cloud9\n</code></pre>\n<p>Run the file in the beaglebone:</p>\n<pre><code class=\"language-bash\">    sudo ./var/lib/cloud9/beagle_demo\n</code></pre>\n<h2>IEC61850 Client</h2>\n<p>Now we have top build the IEC 61850 client that will run in our Ubuntu machine.\nFirst, add this line to beagle<em>client.c for the Thread</em>sleep() function to work:</p>\n<pre><code class=\"language-C\">    #include \"iec61850_client.h\"\n    #include \"hal_thread.h\"\n</code></pre>\n<p>Then, we have to modify the Makefile to build the file for the client:</p>\n<pre><code class=\"language-makefile\">    PROJECT_BINARY_NAME = beagle_client\n    PROJECT_SOURCES = beagle_client.c\n</code></pre>\n<p>Make the beagle_client file:</p>\n<pre><code class=\"language-bash\">    make\n</code></pre>\n<p>Run the beagle_client file:</p>\n<pre><code class=\"language-bash\">    sudo ./beagle_client [IP_ADDRESS_BEAGLE]\n    sudo ./beagle_client beaglebone\n</code></pre>","frontmatter":{"title":"IEC61850 server with Beaglebone Black","author":"Maximiliano Valencia","date":"15 March, 2018"}}},"pathContext":{"slug":"/posts/iec61850_server_beaglebone_black/"}}

